class Prices extends React.Component {
  state = {
    currency: "EUR"
  };
  render() {
    const { bpi } = this.props.bpi;

    let list = "";

    if (this.state.currency === "USD") {
      list = (
        <li className="list-group-item">
          Bitcoin rate for {bpi.USD.description}:
          <span class="badge badge-primary">{bpi.USD.code}</span>
          <strong>{bpi.USD.rate}</strong>
        </li>
      );
    } else if (this.state.currency === "GBP") {
      list = (
        <li className="list-group-item">
          Bitcoin rate for {bpi.GBP.description}:
          <span class="badge badge-primary">{bpi.GBP.code}</span>
          <strong>{bpi.GBP.rate}</strong>
        </li>
      );
    } else if (this.state.currency === "EUR") {
      list = (
        <li className="list-group-item">
          Bitcoin rate for {bpi.EUR.description}:
          <span class="badge badge-primary">{bpi.EUR.code}</span>
          <strong>{bpi.EUR.rate}</strong>
        </li>
      );
    }

    return (
      <div>
        <ul className="list-group">{list}</ul>
        <br />
        <select onChahge={e => this.setState()} className="form-control" />
      </div>
    );
  }
}
export default Prices;
